.products-block {
  position: relative;
  height: 680px;

  &__bg {
    position: absolute;
    right: -15px;
  }

  &__content {
    position: absolute;
    left: 0;
    right: 0;
  }

  &__prev,
  &__next {
    background-color: #fff;
    color: $body-color;
    width: 45px;
    height: 40px;
    display: flex;
    transition: $transition-base;

    &::before {
      content: '';
      width: 8px;
      height: 8px;
      border-top: 2px solid currentColor;
      border-right: 2px solid currentColor;
      margin: auto;
    }
  }

  &__prev {
    border-radius: 20px 0 0 20px;

    &::before {
      transform: rotate(-135deg);
    }
  }

  &__next {
    border-radius: 0 20px 20px 0;

    &::before {
      transform: rotate(45deg);
    }
  }

  &__img {
    position: absolute;
    top: 20px;
    left: 50%;
    transform: translateX(-50%);
    height: 640px;
    width: 720px;
  }

  &__gallery {
    width: 90px;

    .imglist {
      max-height: 405px;
      overflow: hidden;

      a {
        display: block;
        width: 100%;

        &:not(:last-child) {
          margin-bottom: 15px;
        }
      }
    }

    .carousel-video {
      position: relative;

      &::before,
      &::after {
        content: '';
        position: absolute;
        top: 50%;
        left: 50%;
      }

      &::before {
        width: 45px;
        height: 45px;
        border-radius: 50%;
        background-color: #fff;
        transform: translate(-50%, -50%);
      }

      &::after {
        border: 8px solid transparent;
        border-left: 14px solid $black;
        transform: translateY(-50%);
        margin-left: -4px;
      }
    }

    .all-product-img {
      width: 100%;
      display: flex;
      flex-direction: column;
      align-items: center;
    }
  }
}

@include media-breakpoint-down(xl) {
  .product-block {
    &__img {
      width: 88%;
      height: auto;
    }

    &__gallery {
      .all-product-img {
        width: auto;
      }
    }
  }
}
